.nav-btn{
    padding:10px 20px;
    background: linear-gradient(94.37deg, #3387D5 -5.94%, #7A06C9 115.34%),
    linear-gradient(151.31deg, #FFFFFF -57.71%, rgba(255, 255, 255, 0) 82.32%);
    border-radius:30px ;
    color:rgba(255, 255, 255, 1);
    font-weight: 700;
    font-size: 16px;
    border:none;
    margin:0 50px;
}


body {
  background-color: #040406;
}

.token {
  width: 500px;
  min-height: 600px;
  border: 1px solid rgba(217, 217, 217, 0.2);
  border-radius: 10px;
  margin: 70px auto;
  background: #0b0819;
  color: #fff;
}

.img_container {
  width: 100px;
  height: 100px;
  background-color: #0b0819;
  position: relative;
  top: -50px;
  left: 200px;
  z-index: 6;
  border-radius: 50%;
}

.img_container img {
  object-fit: contain;
  width: 100%;
}

#curve {
  width: 120px;
  height: 120px;
  shape-outside: circle();
  -webkit-clip-path: circle(60px at top center);
          clip-path: circle(60px at top center);
  background-color: #040406;
  position: relative;
  left: 190px;
  top: -100px;
  z-index: 3;
}

.token_form {
  display: flex;
  flex-direction: column;
  margin: -130px 15px;
  justify-content: center;
}

.token_form input {
  padding: 20px;
  margin: 10px 5px;
  border-radius: 15px;
  background-color: #0b0819;
  color: #fff;
  border: 1px solid rgba(110, 86, 248, 0.25);
  font-size: 22px;
}
.token_form input:focus {
  outline: none;
}
.token_form input[type="text"]:nth-child(even) {
  background-color: #1c1731;
  border: none;
}

.token_form label {
  width: 100%;
  align-self: flex-start;
  display: flex;
}
.token_form label > span {
  margin-left: auto;
  font-size: 24px;
  font-weight: 700;
}

.token_form button {
  padding: 20px;
  margin: 25px 0;
  border-radius: 50px;
  color: #fff;
  background: linear-gradient(94.37deg, #3387d5 -5.94%, #7a06c9 115.34%),
    linear-gradient(151.31deg, #ffffff -57.71%, rgba(255, 255, 255, 0) 82.32%);
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  border: none;
}

.token_form button:hover {
  cursor: pointer;
}

.symbol_dialog{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
}

::-webkit-scrollbar {display:none;}

.symbol_input{
   width:100%;
   padding: 20px 40px;
   border-radius: 30px;
   background-color:#181627 ;
   border:1px solid rgba(110, 86, 248, 0.25);
   color: #fff;
   font-size: 20px;

}

.symbol_input:focus{
    outline: none;
}

ul{
  list-style-type: none;
  margin: 20px 0;
}
ul li{
    color:#fff;
    font-size: 20px; 
    
}
ul li img{
    margin: 0 0 5px 5px;
    padding: 12px;
    vertical-align: middle;
    width:15%;
}

ul li:hover{
    cursor: pointer;
    background-color: #1B192D;
}

